import * as _ from 'lodash'
// import { applyScripts } from '../utils/applyScripts'

const buttonSound = new Audio(require('../../../static/sound/button.mp3'))
const machineSound = new Audio(require('../../../static/sound/coin.wav'))

const CATEGORIES = ['city', 'nature', 'people']
const gallery = document.querySelector('.js-gallery')

let imgIndex = 0
const maxImgIndex = 7

if (gallery) {
  const galleryCols = gallery.querySelectorAll('.gallery__col__images')

  setTimeout(() => {
    machineSound.play()
  }, 500)

  gallery.checkCats = function () {
    const activeCategories = _.uniq(_.map(galleryCols, 'activeCategory'))
    const isMatching = activeCategories.length === 1

    if (isMatching) {
      gallery.classList.add('is-matching')
      gallery.classList.add(`is-matching--${activeCategories[0]}`)
    } else {
      gallery.classList.remove('is-matching')
      gallery.classList.remove(`is-matching--nature`)
      gallery.classList.remove(`is-matching--people`)
      gallery.classList.remove(`is-matching--city`)
    }
  }

  galleryCols.forEach(galleryCol => {
    galleryCol.images = []

    for (let index = 1; index < maxImgIndex; index++) {
      imgIndex++

      CATEGORIES.forEach(CAT => {
        const img = new Image()
        img.src = require(`../../../static/img/${CAT}/preview/${imgIndex}.jpg`)
        galleryCol.images.push({ cat: CAT, el: img })
      })
    }

    galleryCol.init = function () {
      const randomNumber = _.random(0, this.images.length - 1)
      const prevNumber = this.checkNumber(randomNumber - 1)
      const nextNumber = this.checkNumber(randomNumber + 1)

      const active = this.images[randomNumber]
      const prev = this.images[prevNumber]
      const next = this.images[nextNumber]

      this.appendChild(prev.el)
      this.appendChild(active.el)
      this.appendChild(next.el)

      this.activeCategory = active.cat
      this.currentIndex = randomNumber
      this.reloadDom()
    }

    galleryCol.nextImage = function () {
      this.currentIndex = this.checkNumber(this.currentIndex + 1)
      const activeImage = this.images[this.currentIndex]

      const nextImageIndex = this.checkNumber(this.currentIndex + 1)
      const nextImage = this.images[nextImageIndex]

      this.activeCategory = activeImage.cat
      this.appendChild(nextImage.el)
      this.removeChild(this.firstChild)

      this.reloadDom()
    }

    galleryCol.reloadDom = function () {
      this.childNodes[0].className = ''
      this.childNodes[1].className = ''
      this.childNodes[2].className = ''
      this.childNodes[0].classList.add('prev')
      this.childNodes[1].classList.add('active')
      this.childNodes[2].classList.add('next')

      setTimeout(() => {
        gallery.checkCats()
      }, 400)
    }

    galleryCol.checkNumber = function (number) {
      if (number > -1 && number < this.images.length) {
        return number
      } else if (number === -1) {
        return this.images.length - 1
      } else {
        return 0
      }
    }

    galleryCol.init()
  })

  const buttons = document.querySelectorAll('.button')

  buttons.forEach(button => {
    button.addEventListener('click', () => {
      buttonSound.play()

      const index = button.dataset.index
      galleryCols[index].nextImage()

      activate(button)
      activate(gallery)
      activate(galleryCols[index])
    })
  })
}

function activate (el) {
  el.classList.add('active')

  setTimeout(() => {
    el.classList.remove('active')
  }, 400)
}

