import * as Flickity from 'flickity'
import Vimeo from '@vimeo/player'
import videojs from 'video.js'

const carousel = document.querySelector('.js-carousel')
const playButton = document.querySelector('.js-play')
const pauseButton = document.querySelector('.js-pause')
const prevButton = document.querySelector('.js-prev')
const nextButton = document.querySelector('.js-next')
const fullScreenButton = document.querySelector('.js-fullscreen')
const vimeoButton = document.querySelector('.js-vimeo')

const bp = 768

let currentSlide = {}
let videoLoaded = false

if (carousel) {
  const flkty = new Flickity('.js-slides', {
    prevNextButtons: false,
    pageDots: false,
    wrapAround: true,
    on: {
      ready: function () {
        defineCurrentSlide()
      }
    }
  })

  flkty.on('change', () => {
    if (currentSlide.title) {
      currentSlide.title.innerHTML = ''
    }

    if (currentSlide.vimeo) {
      currentSlide.vimeo.removeEventListener('click', pause)
    }
    if (currentSlide.img) {
      currentSlide.img.removeEventListener('click', play)
    }

    setTimeout(() => {
      pause()
      defineCurrentSlide()
    }, 500)
  })

  playButton.addEventListener('click', () => {
    play()
  })

  pauseButton.addEventListener('click', () => {
    pause()
  })

  prevButton.addEventListener('click', () => {
    flkty.previous()
  })

  nextButton.addEventListener('click', () => {
    flkty.next()
  })

  fullScreenButton.addEventListener('click', () => {
    document.body.classList.toggle('is-fullscreen')
  })

  vimeoButton.addEventListener('click', () => {
    window.open(currentSlide.url, '_blank')
  })
}

async function defineCurrentSlide () {
  currentSlide = {}
  currentSlide.el = document.querySelector('.slide.is-selected')
  currentSlide.img = currentSlide.el.querySelector('.js-image')
  currentSlide.title = currentSlide.el.querySelector('.js-title')
  currentSlide.vimeo = currentSlide.el.querySelector('.video__vimeo')

  if (window.innerWidth >= bp) {
    currentSlide.video = currentSlide.el.querySelector('.video__video--desktop')
  } else {
    currentSlide.video = currentSlide.el.querySelector('.video__video--mobile')
  }

  if (currentSlide.vimeo) {
    carousel.classList.add('is-playable')
    currentSlide.player = new Vimeo(currentSlide.vimeo)
    currentSlide.url = await currentSlide.player.getVideoUrl()

    setTimeout(() => {
      currentSlide.vimeo.addEventListener('click', pause)
      currentSlide.img.addEventListener('click', play)
    }, 4000)

    if (currentSlide.title) {
      let title = await currentSlide.player.getVideoTitle()
      title = title.split(' - ')
      currentSlide.title.innerHTML = `<div>${title[0]}</div><div>${
        title[1]
      }</div>`
    }
  }

  if (currentSlide.video) {
    carousel.classList.remove('is-playable')
    if (!videoLoaded) {
      currentSlide.player = videojs(
        'video',
        { loop: true },
        function onPlayerReady () {
          this.play()
          videoLoaded = true
        }
      )
    }
  }
}

async function play () {
  if (!currentSlide.vimeo) {
    return
  }

  document.body.classList.add('is-playing')
  currentSlide.img.classList.add('is-hidden')
  await currentSlide.player.play()
  currentSlide.player.setVolume(1)
}

async function pause () {
  if (!currentSlide.vimeo) {
    return
  }

  document.body.classList.remove('is-playing')
  currentSlide.img.classList.remove('is-hidden')
  document.body.classList.remove('is-fullscreen')
  await currentSlide.player.unload()
}



